nter; \n    }\n}\n\n.featureImg {\n    width:50%;\n    height: auto;\n    -o-object-fit: cover;\n       object-fit: cover;\n    margin-right: 50px;\n    padding: 10px;\n}\n\n.footer {\n    display: flex;\n    flex-direction: row;\n    justify-items: space-evenly;\n    width: 100%;\n    height: auto;\n    background-color: #000000;\n    color: #ffffff;\n}\n\n// @media .footer\n// screen size smaller, change to column\n\n.footerColumns {\n    display: flex;\n    flex-direction: column;\n    margin-left: 60px;\n    margin-right: 90px;\n    margin-top: 25px;\n    margin-bottom: 150px;\n}\n\n.footerBottom {\n    padding: 10px;\n    background-color: #000000;\n    color: #ffffff;\n}\n\n.badge {\n    width: 90px;\n    height: auto;\n}"],"sourceRoot":""}]);
